import { groupsDetail,Addgroup,queryList } from '@/api/index'
import util from "@/util/util"
export default {
    name: 'applyEntry', // 
    data() {
        return {
            // 
            id:'',//id
            name:'',//
            imgurl:'',//
            num:'',//
            applyTxt:'',
            mask:false,
            toastTxt:'',
            showQrcode:false
        }
    },
    watch: {
        // 
    },
    props: {
        // 
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        this.id=this.$route.query.id;
        groupsDetail(this.id).then(res =>{
            this.imgurl=res.data.imgUrl;
            this.num=res.data.userCount;
            this.name=res.data.groName;
        })
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
       
        // console.group('===============mounted');
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        applyGroup(){
            Addgroup({groId:this.id}).then( res => {
                alert(JSON.stringify(res));
                alert(JSON.stringify(localStorage.getItem('Authorization')))
                if(res.code == 1 ) {
                    this.applyTxt='';
                    this.mask=true;
                    return setTimeout(() => {
                        this.mask = false;
                        this.$router.replace({
                            path:'/nav/card'
                        })
                    }, 1500);
                }else if(res.code==403){
                    this.toastTxt=res.msg;
                    this.mask=true;
                    return setTimeout(() => {
                        this.mask = false;
                    }, 1500);
                }else if(res.code==0){
                    this.toastTxt='';
                    this.mask=true;
                    return setTimeout(() => {
                        this.mask = false;
                        this.showQrcode=true;
                    }, 1000);
                }else{
                    this.toastTxt=res,msg;
                    this.mask=true;
                    return setTimeout(() => {
                        this.mask = false;
                    }, 1500);
                }
            })
        },
        followFn(){//
            let code = util.getCode('code');
            let that=this;
            if(!code){
                this.toastTxt='';
                this.mask=true;
                return setTimeout(() => {
                    this.mask = false;
                }, 1000);
            }else{
                queryList({ code: code }).then(res => {
                    if ( res.code == 1 ) {
                        localStorage.setItem('Authorization', res.data.token);
                        that.showQrcode=false
                    }
                })
            }
        }   
    }

}




